@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  font-family: "Poppins", sans-serif;
  margin:0;
  padding: 0;
}
body{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
}
.quiz-app{
  width:600px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
.question-section{
  text-align: center;
}
.question-section h2{
  margin: 10px 0px;
  font-weight: 600;
  color: #3498db;
}
.options{
display: flex;
justify-content: center;
align-items: center;
gap: 10px;
margin-top: 20px;
}
.question-section p{
  font-weight: 500;
}
button{
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 30px;
  border:none;
  background-color: #3498db;
  color: #fff;
  cursor:pointer;
  transition:  0.4s;
}
button:hover{
  filter: brightness(1.3);
}
.timer{
  margin-top: 20px;
  font-size: 18px;
  color: #555;
}
.timer span{
  font-weight: 800;
}
.score-section{
  text-align: center;
}
.score-section h2{
  font-weight: 500;
  margin-bottom: 15px;
}
.score-section button{
  background-color: tomato;
}